* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-family: "Plus Jakarta Sans", sans-serif;
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1024px;
  margin: 0 auto;
  position: relative;
  padding: 4px 16px;
  background: white;
}

.nav-logo img {
  width: 180px;
}

.nav-desktop-menu {
  display: flex;
  align-items: center;
  gap: 40px;
}
.nav-desktop-menu .nav-links {
  display: flex;
  gap: 32px;
  align-items: center;
  margin: 0;
}
.nav-desktop-menu .nav-links li {
  list-style: none;
}
.nav-desktop-menu .nav-links a {
  color: #000929;
  text-decoration: none;
}
.nav-desktop-menu .nav-links a:hover {
  color: #00796b;
  transition: 0.2s ease-in-out;
}

.nav-auth {
  display: flex;
  gap: 10px;
}
.nav-auth a {
  text-decoration: none;
}
.nav-auth-login {
  padding: 10px 30px;
  color: #00796b;
  transition: all 0.3s ease;
  font-weight: 600;
  border: 1px solid #00796b;
  border-radius: 8px;
}
.nav-auth-login:hover {
  border: 1px solid #000929;
}
.nav-auth-register {
  padding: 10px 20px;
  background: #00796b;
  color: white;
  transition: all 0.3s ease;
  font-weight: 600;
  border: 1px solid transparent;
  border-radius: 8px;
}
.nav-auth-register:hover {
  color: black;
  background: #e0def7;
}

.nav-toggle {
  display: none;
  font-size: 28px;
  cursor: pointer;
  z-index: 200;
}

.nav-mobile-menu {
  display: none;
  flex-direction: column;
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  background-color: white;
  padding: 24px;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.1);
  z-index: 150;
  transition: all 0.3s ease;
}
.nav-mobile-menu.active {
  display: flex;
}
.nav-mobile-menu .nav-links-mobile {
  display: flex;
  flex-direction: column;
  gap: 14px;
  list-style-type: none;
  padding: 0 0 0 20px;
}
.nav-mobile-menu .nav-auth-mobile {
  display: flex;
  flex-direction: column;
  gap: 14px;
}
.nav-mobile-menu a {
  display: flex;
  justify-content: space-between;
  color: #000929;
  text-decoration: none;
  font-size: 18px;
  padding: 8px 0;
}
.nav-mobile-menu a:hover {
  color: #00796b;
}
.nav-mobile-menu .nav-auth-login {
  border: 2px solid #e0def7;
  border-radius: 8px;
  padding: 10px 20px;
  text-align: center;
}
.nav-mobile-menu .nav-auth-register {
  border: 2px solid #00796b;
  border-radius: 8px;
  padding: 10px 20px;
  background: #00796b;
  color: white;
  text-align: center;
}

@media (max-width: 768px) {
  .nav-links,
  .nav-auth {
    display: none !important;
  }
  .nav-toggle {
    display: block;
  }
}
.banner_wrapper {
  height: 100%;
}

.hero_wrapper {
  width: 100%;
  height: 89vh;
  background: #e5e5e5;
  position: relative;
  overflow: hidden;
}
.hero_wrapper::before {
  content: url(../assets/banner.png);
  position: absolute;
  right: -500px;
  bottom: -100px;
  background-repeat: no-repeat;
  background-size: cover;
}
.hero_wrapper .banner_content {
  max-width: 1024px;
  margin: 0 auto;
  height: 100%;
}
.hero_wrapper .banner_content_flex {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.hero_wrapper .banner_content h2,
.hero_wrapper .banner_content p {
  color: #000929;
}

#property-search-form {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: 8px;
  max-width: 400px;
  color: #000929;
}
#property-search-form input,
#property-search-form select,
#property-search-form button {
  padding: 10px 12px;
  font-size: 15px;
  border: 1px solid #00796b;
  border-radius: 6px;
  transition: border-color 0.3s ease;
  color: #000929;
  background-color: white;
}
#property-search-form input::placeholder,
#property-search-form select {
  color: #000929;
}
#property-search-form input:focus,
#property-search-form select:focus {
  outline: none;
  border-color: #00796b;
}
#property-search-form select {
  appearance: none;
  cursor: pointer;
}
#property-search-form .form-row {
  display: flex;
  gap: 8px;
}
#property-search-form .form-row input {
  width: 50%;
}
#property-search-form button {
  background-color: #00796b;
  color: white;
  font-weight: 700;
  cursor: pointer;
  border: none;
  border-radius: 6px;
  padding: 12px 0;
  transition: background-color 0.3s ease;
}
#property-search-form button:hover {
  background-color: #00796b;
}